import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
const ORDER_LIST = 'wms/storageLocation'

@connect(
  state => ({...state.storageLocation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '序号',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '商品条码',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '商品名称',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '单价（元）',
      dataIndex: 'price',
      key: 'price',
      align: 'center'
    }, {
      title: '收货单位',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '计划量',
      dataIndex: 'planQty',
      key: 'planQty',
      align: 'center'
    }, {
      title: '已收量',
      dataIndex: 'receiveQty',
      key: 'receiveQty',
      align: 'center'
    }, {
      title: '金额总计（元）',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error('订单编号错误'))
      // ids.map((o) => {
      //   reqData.push({
      //     id: o
      //   })
      // })
      const {handleGetDetailPrint} = this.props
      handleGetDetailPrint({data: ids})
    } catch (err) {
      console.error(err)
    }
  }
  /* 关闭窗口 */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /* 打印 */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
  }

  // 画条形码
  componentDidUpdate() {
    const {printData} = this.props
    printData.map((o) => {
      JsBarcode(`#key${o.key}`, o.key, {
        width: 2,
        height: 60,
        displayValue: false
      })
    })
  }
  render() {
    const {printData, printLoading} = this.props
    return (
      <React.Fragment>
        {
          printData.map((o, i) => <div className="storageLocationPrint clearfix" key={o.key}>
                    <h2>{o.assembleKey}</h2>
                    <img id={`key${o.key}`}/>
                  </div>)
        }
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button> : (printLoading ? <h2 style={{textAlign: 'center'}}>正在加载打印数据</h2> : <h2 style={{textAlign: 'center'}}>暂无打印数据</h2>)
        }
        <div className="printLoadingSty">
          <Spin spinning={printLoading} size="large"/>
        </div>
      </React.Fragment>
    )
  }
}



