import * as actionType from './actionType'
import {getData} from 'util'
import {API_STORAGELOCATIONS_PAGE, API_STORAGELOCATIONS_CREATE, API_STORAGELOCATIONS_UPDATE, API_STORAGELOCATIONS_UPDATESTATUS, API_PRODUCT_INIT, API_STORAGELOCATIONS_EXPORT, API_STORAGELOCATIONS_PRINT, API_STORAGELOCATIONS_EXPORT_PDF} from 'api'
//同步
export const update = (object) => ({
  type: actionType.STORAGELOCATION_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.STORAGELOCATION_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_STORAGELOCATIONS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.STORAGELOCATION_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}

// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.STORAGELOCATION_UPDATE, data: {modalTuttonLoading: true}})
  let api = reqData.data && reqData.data.id ? API_STORAGELOCATIONS_UPDATE : API_STORAGELOCATIONS_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STORAGELOCATION_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}

// 启用禁用仓库
export const handleOperator = (reqData, callBack) => (dispatch) => {
  getData({api: API_STORAGELOCATIONS_UPDATESTATUS, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}

// 初始化
export const handleInit = (reqData, callBack) => (dispatch) => {
  getData({api: API_PRODUCT_INIT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_STORAGELOCATIONS_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}

// 导出PDF
export const handleExportPDF = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_STORAGELOCATIONS_EXPORT_PDF, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}

// 打印收货单
export const handleGetDetailPrint = (reqData) => (dispatch) => {
  dispatch({type: actionType.STORAGELOCATION_UPDATE, data: {printLoading: true}})
  getData({api: API_STORAGELOCATIONS_PRINT, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STORAGELOCATION_UPDATE,
      data: {
        printLoading: false,
        printData: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STORAGELOCATION_FILE
    })
  })
}



